<template>
  <div id="HomePage">
    <!-- 轮播图 -->
    <div id="swiper" class="container-fuild">
      <div class="swiper-container banner-swiper">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="(item,index) in swiperList" :key="index">
            <img class="swiper-lazy" :data-src="item.img" alt="轮播图">
            <div class="swiper-lazy-preloader"></div>
            <div class="swiper-slide-title">
                <h1>{{item.title}}</h1>
                <p>{{item.content}}</p>
            </div>
          </div>
        </div>
        <!-- 如果需要分页器 -->
        <div class="swiper-pagination"></div>

        <!-- 如果需要导航按钮 -->
        <div class="swiper-button-prev"></div>
        <div class="swiper-button-next"></div>
      </div>
    </div>
    <!-- 介绍页面 -->
    <div id="bigData" class="container-fuild">
      <div class="row bigData-container">
        <div class="col-xs-12 col-sm-12 col-md-6 wow zoomIn">
          <img class="img-responsive" src="@/assets/img/banner1.jpg" alt="大数据管理系统">
           <img class="img-responsive" src="@/assets/img/banner4.png" alt="大数据管理系统">
        </div>
        <div class="col-xs-12 col-sm-12 col-md-6">
          <h2 class="bigData-title">
            碳排放与空气质量遥感团队
            <small>/AERSC@CUMT</small>
          </h2>
          <p>Atmospheric Environmental Remote Sensing Cooperative</p>
          <p class="text-justify">The carbon emission and air quality remote sensing team is co-led by Professor Qin Kai and Professor Jason Cohen, and includes 2 professors, 3 associate professors, 3 research assistants/postdoctoral fellows/experimenters, and more than 30 doctoral/master students. Relying on the advantageous discipline of surveying and mapping science and technology (A-) and the Jiangsu Provincial Key Laboratory of Coal-based Greenhouse Gas Emission Reduction and Resource Utilization, it is oriented to the needs of carbon emission reduction and air quality improvement, focusing on the environment and climate of the entire energy production and consumption chain. Effect, use sky-air-ground observation methods to carry out research on "remote sensing inversion-emission quantification-impact attribution".</p>
          <p>碳排放与空气质量遥感团队由秦凯教授和Jason Cohen教授共同领导，包括教授2人，副教授3人，助研/博士后/实验师3人，博士/硕士研究生30余名。依托测绘科学与技术优势学科（A-）和江苏省煤基温室气体减排与资源化利用重点实验室等平台，面向碳减排与空气质量改善需求，聚焦能源生产与消费全链条环境与气候效应，利用天-空-地观测手段，开展“遥感反演-排放量化-影响归因”研究。</p>
          <br>
          <br>
          <router-link :to="{path:'/contactus'}" class="btn btn-lg btn-block btn-info">查看更多</router-link>
        </div>
      </div>
    </div>
    <!-- 您身边的IT专家 -->
    <!-- <div class="container">
      <div class="jumbotron jumbotron-fluid mb-3 pt-0 pb-0 bg-lightblue position-relative">
        <div class="pl-4 pr-0 h-100 tofront">
          <div class="row justify-content-between">
            <div class="col-md-6 pt-6 pb-6 align-self-center">
              <h1 class="bigData-title">Atmospheric Environmental Remote Sensing Cooperative<small>/AERSC@CUMT</small></h1>
              <p class="mb-3">
                碳排放与空气质量遥感团队由秦凯教授和Jason Cohen教授共同领导，包括教授2人，副教授3人，助研/博士后/实验师3人，博士/硕士研究生30余名。依托测绘科学与技术优势学科（A-）和江苏省煤基温室气体减排与资源化利用重点实验室等平台，面向碳减排与空气质量改善需求，聚焦能源生产与消费全链条环境与气候效应，利用天-空-地观测手段，开展“遥感反演-排放量化-影响归因”研究。
              </p>
              <a href="./article.html" class="btn btn-dark">Read More</a>
            </div>
            <div class="col-md-6 d-none d-md-block pr-0" style="background-size:cover;background-image:url(../assets/img/640.png);">	</div>
          </div>
        </div>
      </div>
    </div> -->
    <!-- 课组成果 -->
    <div id="customer" class="container-fuild ">
      <div class="container customer-container">
        <p class="customer-title text-center  text-uppercase">Research group results</p>
         <p class="text-center"><small>课组成果</small></p>
        <div class="swiper-container customer-swiper hidden-xs" v-if=" customerList.length">
          <div class="swiper-wrapper">
            <div
              class="swiper-slide customer-block"
              v-for="(item,index) in customerList"
              :key="index"
              @click="jumpTo(item)"
            >
              <div class="customer-logo">
                <img class="center-block" :src="item.logo" alt="logo">
              </div>
              <!-- <div class="customer-yh">
                <img src="@/assets/img/yinhao.png" alt="引号">
              </div> -->
              <div class="customer-content1"></div>
              <div class="customer-content2">{{item.chtittle}}</div>
              <small class="bigData">{{ item.tittle }}</small>
            </div>
          </div>
          <!-- 如果需要导航按钮 -->
          <div class="swiper-button-prev"></div>
          <div class="swiper-button-next"></div>
        </div>
        <div class="row visible-xs customer-block">
          <div class="col-xs-12" v-for="(item,index) in customerList" :key="index" @click="jumpTo(item)">
            <div class="customer-logo">
              <img class="center-block" :src="item.logo" alt="logo">
            </div>
            <!-- <div class="customer-yh">
              <img src="@/assets/img/yinhao.png" alt="引号">
            </div> -->
            <div class="customer-content1">
              <small>{{item.chtittle}}</small>
            </div>
            <div class="customer-content2">
              <small>{{item.tittle }}</small>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 课题组近况--> 
    <div id="whyChooseUs" class="conatiner-fuild">
      <div class="container">
        <div class="whyChooseUs-title text-center">
          <p class="text-uppercase">Current status of the research group</p>
          <p>课题组近况</p>
          <a href="#/newsinformation" role="button" class="learn">Learn more>>></a>
        </div>
        <div class="row wow fadeInUpBig">
          <div
            class="col-xs-12 col-sm-6 col-md-3 server-wrapper"
            v-for="(item,index) in serverList"
            :key="index"
          >
            <div
              class=" jumbotron server-block wow slideInUp"
              onmouseenter="this.style.color='#28f';this.style.borderColor='#28f'"
              onmouseleave="this.style.color='#666';this.style.borderColor='#ccc'"
              @click="jumpToNews(item)"
            >
              <img class="center-block img-responsive" :src="item.logo" alt="logo">
              <p class="text-center">{{item.tittle}}</p>
              <!-- <div
                class="text-center"
                v-html="item.content"
                onmouseenter="this.style.color='#28f'"
                onmouseleave="this.style.color='#ccc'"
              ></div> -->
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- jik -->
  </div>
</template>
<script>
import Swiper from "swiper";
import { WOW } from 'wowjs';
import Data from '@/assets/data/research.json';
import news from '../assets/data/news.json';
export default {
  name: "HomePage",
  data() {
    return {
      news ,
      swiperList: [
        {
          img: require("@/assets/img/cal1.jpg"),
          path: "",
          title: 'Professor Qin Kai made a report',
          content: '宣传简介宣传简介宣传简介宣传简介宣传简介宣传简介宣传简介宣传简介',
        },
        {
          img: require("@/assets/img/banner3.png"),
          path: "",
          title: 'Professor Jason Cohen is giving an academic presentation',
          content: '宣传简介宣传简介宣传简介宣传简介宣传简介宣传简介宣传简介宣传简介',
        },
        {
          img: require("@/assets/img/banner2.png"),
          path: "",
          title: 'Members of the research group',
          content: '宣传简介宣传简介宣传简介宣传简介宣传简介宣传简介宣传简介宣传简介',
        },
      ],
      customerList: [],
      serverList: {},
    };
  },
  Created(){
  
  },
  methods:{
    jumpTo(e){
        this.$router.push({path:'/publiaction/publicationDetail/'+ e.number,query:e});
    },
    jumpToNews(e){
      this.$router.push({path:'/newsinformation/details' + e.ID, query:e});
    },
    initSwiper(){
       /* customer-swiper */
        new Swiper(".customer-swiper", {
          loop: true, // 循环模式选项
          slidesPerView: 3,
          //自动播放
          autoplay: {
            delay: 5000,
            stopOnLastSlide: false,
            disableOnInteraction: false
          },
          // 如果需要前进后退按钮
          navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev"
          },
          observer: true, //修改swiper自己或子元素时，自动初始化swiper
          observeParents: true //修改swiper的父元素时，自动初始化swiper
        });
    }
  },
  mounted() {
    this.customerList = Data.Sheet1;
    this.serverList = this.news.Sheet1.slice(-4);
    /* banner-swiper */
    new Swiper(".banner-swiper", {
      loop: true, // 循环模式选项
      effect: 'fade',
      //自动播放
      autoplay: {
        delay: 3000,
        stopOnLastSlide: false,
        disableOnInteraction: false
      },
      // 如果需要分页器
      pagination: {
        el: ".swiper-pagination",
        clickable: true
      },
      // 如果需要前进后退按钮
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev"
      },
      // 延迟加载
      lazy: {
        loadPrevNext: true
      },
      observer: true, //修改swiper自己或子元素时，自动初始化swiper
      observeParents: true //修改swiper的父元素时，自动初始化swiper
    });
    this.$nextTick(() => {
          this.initSwiper()
        })

    /* wowjs动画 */
    var wow = new WOW({
      boxClass: 'wow',
      animateClass: 'animated',
      offset: 0,
      mobile: true,
      live: true
    })
    wow.init();
  }
};
</script>
<style scoped>
/* 整体盒子 */
#HomePage {
  width: 100%;
}

/* 轮播图 */
#swiper {
  background-color: #efefef;
  height: 700px;
}
#swiper .banner-swiper {
  width: 80%;
  height: 100%;

}
#swiper .banner-swiper .swiper-slide img {
  width: 100%;
  height: 100%;
}
#swiper .banner-swiper .swiper-slide{
  position: relative;
}
#swiper .banner-swiper .swiper-slide-title {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 999999999;
  width:inherit;
  height: 10%;
  color: hwb(0 100% 0%);
  background-color: rgba(116, 116, 116, 0.521);
  text-align: center;
  line-height: 80px;
}
#swiper .banner-swiper .swiper-slide-title > h1{
  font-size: 24px;
  margin-bottom: 12%;
  font-weight: 700;
}
#swiper .banner-swiper .swiper-slide-title > p{
  font-size: 14px;
  margin-bottom: 1%;
  font-weight: 700;
}
/* 大数据管理系统 */
#bigData {
  padding: 10vh;
  transition: all ease 0.6s;
  box-sizing: border-box;
}
#bigData .bigData-title {
  padding-bottom: 3vh;
  border-bottom: 1px solid #ccc;
  font-size: 4vh;
}
#bigData p {
  font-size: 2vh;
  color: #333;
  line-height: 1.8;
}
#bigData .bigData-device {
  margin: 50px 0 20px;
}

/* 您身边的IT专家 */
#contactUs {
  color: #fff;
  height: 400px;
  width: 80%;
  background: url("../assets/img/contact_us_bg.jpg") 0 0 no-repeat;
  background-size: 100% 100%;
  transition: all ease 0.6s;
  margin-left: 10%;
}
#contactUs .contactUs-container {
  padding-top: 50px;
}
#contactUs .contactUs-container button {
  width: 300px;
  height: 50px;
  margin-top: 40px;
}
#contactUs .contactUs-container .contactUs-contactWay span {
  display: inline-block;
  width: 48px;
  height: 48px;
  margin: 30px;
}
#contactUs .contactUs-container .contactUs-contactWay span:nth-of-type(1) {
  background: url("../assets/img/weixin.png") 0 0 no-repeat;
  background-size: 100% 100%;
}
#contactUs .contactUs-container .contactUs-contactWay span:nth-of-type(2) {
  background: url("../assets/img/weibo.png") 0 0 no-repeat;
  background-size: 100% 100%;
}
#contactUs .contactUs-container .contactUs-contactWay span:nth-of-type(3) {
  background: url("../assets/img/twitter.png") 0 0 no-repeat;
  background-size: 100% 100%;
}

/* 课组成果 */
#customer {
  padding: 50px 0;
  box-sizing: border-box;
  background: #efefef;
  transition: all ease 0.8s;
}
#customer .customer-container {
    width: 100%;
}
#customer .swiper-slide{
    height: 60vh;	
}
#customer .customer-title {
  font-size: 30px;
  color: rgb(102, 102, 102);
  margin: 0 0 30px;
}
#customer .customer-block {
  background: #fff;
  padding: 30px;
}
#customer .customer-logo{
  overflow: hidden;
  cursor: pointer;
}
#customer .customer-logo img {
  width: 45vh;
  height: 25vh;
  border: 1px solid #ccc;
  -webkit-transform: scale(1);
	transform: scale(1);
	-webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
  object-fit: contain;
  
}
#customer .customer-logo img:hover{
  -webkit-transform: scale(1.3);
	transform: scale(1.3);
}
#customer .customer-yh img {
  width: 24px;
  height: 24px;
}
#customer .customer-content1 {
  padding-bottom: 5vh;
  border-bottom: 1px solid #0ce9f1;
}
#customer .customer-content2 {
  padding-top: 5vh;
  font: 3vh sans-serif;
}
#customer small{
  font: 2vh sans-serif;
  color: rgb(121, 118, 118);

}
#customer .swiper-slide::before,#customer .swiper-slide::after {
  box-sizing: inherit;
  position: absolute;
  content: '';
  border: 2px solid transparent;
  width: 0;
  height: 0;
}

#customer .swiper-slide::after {
  bottom: 0;
  right: 0;
}

#customer .swiper-slide::before {
  top: 0;
  left: 0;
}

#customer .swiper-slide::before, #customer .swiper-slide:hover::after {
  width: 100%;
  height: 100%;
}

#customer .swiper-slide:hover::before {
  border-top-color: #4361ee;
  border-right-color: #4361ee;
  transition: width 0.3s ease-out, height 0.3s ease-out 0.3s;
}
#customer .swiper-slide:hover::after {
  border-bottom-color: #4361ee;
  border-left-color: #4361ee;
  transition: border-color 0s ease-out 0.6s, width 0.3s ease-out 0.6s, height 0.3s ease-out 1s;
}


/* 近况 */
#whyChooseUs {
  padding: 100px;
}
#whyChooseUs .whyChooseUs-title {
  margin-bottom: 50px;
}
#whyChooseUs .whyChooseUs-title p:nth-of-type(1) {
  font-size: 25px;
  font-weight: 500;
}
#whyChooseUs .whyChooseUs-title p:nth-of-type(2) {
  font-size: 14px;
}
#whyChooseUs .server-block {
  padding: 50px 20px;
  border: 1px solid #ccc;
  border-bottom: 5px solid #ccc;

}
#whyChooseUs .server-block img {
  width: 100%;
  height:auto;
  display: block;
}
#whyChooseUs .server-block > p {
  font-size: 20px;
  margin: 30px 0;
}
#whyChooseUs .server-block > div {
  color: #ccc;
}
.learn{
  float:right;
}
/* 媒体查询（手机） */
@media screen and (max-width: 768px) {
  #swiper {
    height: 200px;
  }
  #bigData {
    padding: 30px;
  }
  #bigData .bigData-title {
    font-size: 20px;
  }
  #bigData .bigData-device {
    font-size: 20px;
    margin: 10px 0 10px;
  }
  #contactUs {
    height: 200px;
    transition: all ease 0.6s;
  }
  #contactUs .contactUs-container {
    padding-top: 0;
  }
  #contactUs .contactUs-container h1 {
    font-size: 25px;
  }
  #contactUs .contactUs-container h3 {
    font-size: 18px;
  }
  #contactUs .contactUs-container button {
    width: 200px;
    height: 30px;
    margin-top: 20px;
  }
  #contactUs .contactUs-container .contactUs-contactWay span {
    display: inline-block;
    width: 28px;
    height: 28px;
    margin: 10px;
  }
  #customer {
    padding: 30px 0;
    box-sizing: border-box;
    background: #fff;
  }
  #customer .customer-title {
    font-size: 16px;
    font-weight: bold;
  }
  #customer .customer-logo img {
    max-width: 100%;
    max-height:100%;

  }
  #customer .customer-block {
    padding: 30px;
  }
  #customer .customer-block > div {
    padding: 30px 0;
  }
  #whyChooseUs {
    padding: 20px 0;
    transition: all ease 0.6s;
  }
  #whyChooseUs .whyChooseUs-title p:nth-of-type(1) {
    font-size: 20px;
    font-weight: 700;
  }
  #whyChooseUs .whyChooseUs-title p:nth-of-type(2) {
    font-size: 12px;
  }
  #whyChooseUs .server-block {
    padding: 50px 0;
    border: 1px solid #ccc;
    border-bottom: 5px solid #ccc;
  }
  #whyChooseUs .server-block img {
    width: 100%;
    height:auto;
    display: block;
  }
  #whyChooseUs .server-block > p {
    font-size: 20px;
    margin: 30px 0;
  }
  #whyChooseUs .server-block > div {
    color: #ccc;
  }
}

/* 媒体查询（平板） */
@media screen and (min-width: 768px) and (max-width: 996px) {
  #swiper {
    height: 400px;
  }
  #bigData {
    padding: 60px;
  }
  #bigData .bigData-title {
    font-size: 30px;
  }
  #bigData .bigData-device {
    font-size: 30px;
    margin: 30px 0 15px;
  }
  #contactUs {
    height: 300px;
  }
  #contactUs .contactUs-container {
    padding-top: 50px;
  }
  #contactUs .contactUs-container h1 {
    font-size: 30px;
  }
  #contactUs .contactUs-container h3 {
    font-size: 20px;
  }
  #contactUs .contactUs-container button {
    width: 300px;
    height: 50px;
    margin-top: 30px;
  }
  #contactUs .contactUs-container .contactUs-contactWay span {
    display: inline-block;
    width: 32px;
    height: 32px;
    margin: 15px;
  }
  #customer .customer-title {
    font-size: 24px;
  }
  #whyChooseUs {
    padding: 20px 0;
  }
}
</style>




